.g-1 {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 1rem; }

.g-1 > * {
  background-color: var(--color-primary-light-x);
  padding: 1.5rem;
  border-radius: .5rem; }

.g-2 {
  display: grid;
  grid-gap: 1rem; }
  @media only screen and (min-width: 768px) {
    .g-2 {
      grid-template-columns: repeat(auto-fill, minmax(calc(50% - 1rem), 1fr)); } }
  @media only screen and (min-width: 1025px) {
    .g-2 {
      grid-template-columns: repeat(auto-fill, minmax(calc(25% - 1rem), 1fr)); } }

.g-2 > * {
  background-color: var(--color-primary-light-x);
  padding: 1.5rem;
  border-radius: .5rem; }

.g-3 {
  display: grid;
  grid-gap: 1rem; }
  @media only screen and (min-width: 1025px) {
    .g-3 {
      grid-template-columns: repeat(auto-fill, minmax(calc(50% - 1rem), 1fr)); } }

.colors-primary,
.colors-secondary {
  display: grid;
  grid-gap: 1rem;
  padding-bottom: 1rem; }
  .colors-primary > div,
  .colors-secondary > div {
    background-color: #eee;
    padding: 1.5rem;
    border-radius: .5rem; }
    .colors-primary > div:nth-child(1),
    .colors-secondary > div:nth-child(1) {
      grid-column: span 4;
      background-color: var(--color-primary);
      color: white; }
    .colors-primary > div:nth-child(2),
    .colors-secondary > div:nth-child(2) {
      color: white;
      background-color: var(--color-primary-dark); }
    .colors-primary > div:nth-child(3),
    .colors-secondary > div:nth-child(3) {
      color: white;
      background-color: var(--color-primary-dark-m); }
    .colors-primary > div:nth-child(4),
    .colors-secondary > div:nth-child(4) {
      color: white;
      background-color: var(--color-primary-dark-x); }
    .colors-primary > div:nth-child(5),
    .colors-secondary > div:nth-child(5) {
      color: white;
      background-color: var(--color-primary-dark-xx); }
    .colors-primary > div:nth-child(6),
    .colors-secondary > div:nth-child(6) {
      background-color: var(--color-primary-light); }
    .colors-primary > div:nth-child(7),
    .colors-secondary > div:nth-child(7) {
      background-color: var(--color-primary-light-m); }
    .colors-primary > div:nth-child(8),
    .colors-secondary > div:nth-child(8) {
      background-color: var(--color-primary-light-x); }
    .colors-primary > div:nth-child(9),
    .colors-secondary > div:nth-child(9) {
      background-color: var(--color-primary-light-xx); }

.colors-secondary > div:nth-child(1) {
  grid-column: span 4;
  background-color: var(--color-secondary);
  color: white; }

.colors-secondary > div:nth-child(2) {
  color: white;
  background-color: var(--color-secondary-dark); }

.colors-secondary > div:nth-child(3) {
  color: white;
  background-color: var(--color-secondary-dark-m); }

.colors-secondary > div:nth-child(4) {
  color: white;
  background-color: var(--color-secondary-dark-x); }

.colors-secondary > div:nth-child(5) {
  color: white;
  background-color: var(--color-secondary-dark-xx); }

.colors-secondary > div:nth-child(6) {
  background-color: var(--color-secondary-light); }

.colors-secondary > div:nth-child(7) {
  background-color: var(--color-secondary-light-m); }

.colors-secondary > div:nth-child(8) {
  background-color: var(--color-secondary-light-x); }

.colors-secondary > div:nth-child(9) {
  background-color: var(--color-secondary-light-xx); }

